'use strict';

/**
* Compute the secant of a single-precision floating-point number (in degrees).
*
* @module @stdlib/math-base-special-secdf
*
* @example
* var secdf = require( '@stdlib/math-base-special-secdf' );
*
* var v = secdf( 30.0 );
* // returns ~1.15
*
* v = secdf( 45.0 );
* // returns ~1.41
*
* v = secdf( 60.0 );
* // returns ~2.0
*
* v = secdf( 90.0 );
* // returns Infinity
*
* v = secdf( 0.0 );
* // returns 1.0
*
* v = secdf( NaN );
* // returns NaN
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
